<script>
import Header from "../../components/global/header/HeaderArea.vue"
import CitySelecter from "./components/CitySelecter.vue"
import { useRouter } from "vue-router"
import {Toast} from '../../components/global/swal.js'
import { ref, onMounted, watch } from 'vue'
import GoogleMap from './components/GoogleMap.vue'
import hospitalImg from '../../assets/icon/hospital.png'
import { apiHospital,apiHospitalAllCity } from "@/api/api"

export default {
  setup() {
    const router = useRouter()
    let ImgUrl = hospitalImg
    let hospitalData2 = ref([])
    let cityIdx = ref(0)
    let areaIdx = ref(0)

    // hospitalData = hospital.data

    //使用者預設位置
    let initial = ref([120.6802474, 24.158286989])
   
    const updateCityIdx = (val) => {
      cityIdx.value = val.value
    }
    const updateAreaIdx = (val) => {
      areaIdx.value = val.value
    }

    watch(() => cityIdx.value, async () => {
      hospitalData2.value = await apiHospitalAllCity({cityId:cityIdx.value})
      hospitalData2.value = hospitalData2.value.data
    })
    watch(() => areaIdx.value, async () => {
      hospitalData2.value = await apiHospital({cityId:cityIdx.value, areaId:areaIdx.value})
      if(hospitalData2.value.data.length == 0){
        Toast("該區域無資料")
        hospitalData2.value = []
      }
      hospitalData2.value = hospitalData2.value.data
    })

    return { hospitalData2, initial, ImgUrl, updateCityIdx, updateAreaIdx }
  },
  components: {
    Header, GoogleMap, CitySelecter
  },
};
</script>

<template>
  <Header title="醫院" right="text" path="/map/hospital" />
  <div class="hospital">
    <section class="c-main">
      <CitySelecter @city-idx="updateCityIdx" @area-idx="updateAreaIdx" />
      <div class="resource-container">
        <div class="map-container">
          <!-- 地圖 -->
          <GoogleMap class="map" :data=hospitalData2 :initial=initial :icon=ImgUrl />
        </div>
      </div>

    </section>
  </div>
</template>


<style lang="scss" scoped>
@import "@/assets/custom/config";

.hospital {
  margin-top: $header-height;
  margin-bottom: calc($footer-height + 15px);
}

.c-main {
  //避免托動到map問題
  touch-action: none;
}

.map {
  width: 100%;
  height: calc(100vh - $footer-height - $header-height - 3rem);
  touch-action: none;
}
</style>