<script>
import { reactive, ref, onMounted, watch } from 'vue'

export default {
    props: {
        data: Object, //API整個資料
        icon: String, //icon圖片位置
        initial: Array //地圖中心點
    },
    setup(props) {
        // const { data, icon } = props
        const data = reactive(props.data)
        const icon = ref(props.icon)
        // let hospitalPoint = reactive(data)
        let map = null
        let lat = ref(props.initial[1])
        let lng = ref(props.initial[0])

        let points = reactive([])
        let mapData = reactive({
            "center": {
                "lat": lat.value,
                "lng": lng.value
            },
            "lacation": points
        })
        // 地點位置
        const getPoints = () => {
            mapData.lacation = []
            props.data.forEach(item => {
                mapData.lacation.push({
                    "id": item.properties.id,
                    "name": item.properties.name,
                    "lat": item.geometry.coordinates[1],
                    "lng": item.geometry.coordinates[0],
                    "telephone": item.properties.telephone,
                    "address": item.properties.address,
                    "website": item.properties.website
                })
            })
        }

        //建立地圖
        const initMap = (lat, lng) => {
            map = new google.maps.Map(document.getElementById("map"), {
                center: { lat, lng },
                zoom: 15,
                maxZoom: 20,
                minZoom: 3,
                streetViewControl: false,
                mapTypeControl: false
            });
            return map
        }
        // 建立地標
        const setMarker = () => {
            // 為每間餐廳都建立地標、訊息視窗、事件監聽
            mapData.lacation.forEach(item => {
                const marker = new google.maps.Marker({
                    position: { lat: item.lat, lng: item.lng },
                    map: map,
                    icon: icon.value
                });
                // 訊息視窗
                const infowindow = new google.maps.InfoWindow({
                    content: `
                        <div id="content">
                            <p class="infoHeader" style="color:#8F63CF ; font-size:1rem; font-weight:600;" >${item.name}<a style="margin:0;font-size:0.8rem; font-weight:400;" href='${item.website}'">(網站)</a></p>
                            <p class="infoBody" style="margin:0;font-size:0.75rem; font-weight:400;" v-if="item.address" >${item.address}
                                
                            </p>
                            
                        </div>
                        `,
                    maxWidth: 300
                });
                // 綁定點擊事件監聽
                marker.addListener("click", () => {
                    const ele = document.querySelector('.gm-style-iw')
                    // console.log(ele)
                    if (ele) {
                        const eleP = ele.parentNode.classList[0]
                        document.querySelector(`.${eleP}`).remove()
                    }
                    infowindow.open(map, marker);
                });
            });
        }


        onMounted(() => {
            initMap(lat.value, lng.value)
            getPoints()
            setMarker()
        })
        watch(() => props.initial, (val) => {
            lat.value = (val[1])
            lng.value = (val[0])
            initMap(lat.value, lng.value)
            getPoints()
            setMarker()
        })
        watch(() => props.data, () => {
            if (props.data[0]) {
                lat.value = (props.data[0].geometry.coordinates[1])
                lng.value = (props.data[0].geometry.coordinates[0])
                initMap(lat.value, lng.value)
                getPoints()
                setMarker()
            }
        })
        return { map, lat }
    },
};
</script>

<template>
    <div>
        <div class="google-map" id="map"></div>
    </div>
</template>


<style lang="scss" scoped>
@import "@/assets/custom/config";

.google-map {
    width: 100%;
    height: calc(100vh - $footer-height - $header-height - 3rem);
    // border: 10px solid red;
}
</style>